import vcInput from "./input";
import { VNode, App } from "vue3";

export interface InputProps {
  type?:
    | "text"
    | "password"
    | "textarea"
    | "url"
    | "email"
    | "date"
    | "number"
    | "tel";
  //
  status?: "success" | "error" | "warning" | "info";
  //  v-model 
  value?: string | number;
  /** largesmalldefault */
  size?: "" | "large" | "small" | "default";
  /**  */
  name?: string;
  placeholder?: string;
  /**  */
  clearable?: boolean;
  /**  */
  disabled?: boolean;
  /**  */
  readonly?: boolean;
  /**  */
  maxlength?: number;
  /**  text  */
  icon?: string;
  /**  */
  prefix?: string;
  /**  */
  suffix?: string;
  /**  */
  search?: boolean;
  /**  search  */
  enterButton?: boolean | string;
  /**  textarea  */
  rows?: number;
  /**  textarea  { minRows: 2, maxRows: 6 } */
  autosize?: boolean | { minRows?: number; maxRows?: number };
  /**  */
  autofocus?: boolean;
  /**  off  on,off */
  autocomplete?: string;
  /**  spellcheck  */
  spellcheck?: boolean;
  /**  wrap  hard  soft textarea  */
  wrap?: "hard" | "soft";
  /**  text  */
  preEl?: VNode[];
  /**  text  */
  afterEl?: VNode[];
  /**  */
  onEnter?(event: KeyboardEvent): void;
  /**  icon  */
  onClick?(): void;
  /**  */
  onChange?(event: any): void;
  /**  */
  onFocus?(): void;
  /**  */
  onBlur?(): void;
  /**  keyup  */
  onKeyup?(event: KeyboardEvent): void;
  /**  keydown  */
  onKeydown?(event: KeyboardEvent): void;
  /**  keypress  */
  onKeypress?(event: KeyboardEvent): void;
  /**  search  */
  onSearch?(value: string): void;
  /**  clearable  */
  onClear?(): void;
}

/**
 * @see [`docs`](https://baidu.com)
 *
 * `props`
 * ```
 * type: { type: String, default: 'text' }, //  v-model 
 * value: { type: [String, Number], default: '' },
 * size: { type: String, default: 'default' },
 * placeholder: { type: String, default: '' }, // 
 * maxlength: Number,
 * disabled: { type: Boolean, default: false }, // 
 * icon: String,
 * autosize: { type: [Boolean, Object], default: false },
 * rows: { type: Number, default: 2 },
 * readonly: { type: Boolean, default: false },
 * name: String,
 * autofocus: { type: Boolean, default: false },
 * spellcheck: { type: Boolean, default: false },
 * autocomplete: { type: String, default: 'off' },
 * clearable: { type: Boolean, default: false }, // 
 * elementId: { type: String },
 * wrap: ({ type: String, default: 'soft' } as any) as PropType<'hard' | 'soft'>,
 * prefix: { type: String, default: '' },
 * suffix: { type: String, default: '' },
 * search: { type: Boolean, default: false },
 * enterButton: { type: [Boolean, String], default: false },
 * afterEl: { type: Object },
 * preEl: { type: Object }
 * ```
 * `event`
 * ```
 * onEnter?(event: KeyboardEvent): void//
 * onClick?(): void// icon 
 * onChange?(event: any): void //
 * onFocus?(): void//
 * onBlur?(): void //
 * onKeyup?(event: KeyboardEvent): void // keyup 
 * onKeydown?(event: KeyboardEvent): void// keydown 
 * onKeypress?(event: KeyboardEvent): void // keypress 
 * onSearch?(value: string): void // search 
 * onClear?(): void// clearable 
 * ```
 */
const Input = vcInput;
// tslint:disable-next-line: whitespace align
(Input as any).install = (app: App) => {
  console.log(app);
  app.component("V" + Input.name, Input as any);
};

export default Input;
